.p-gnav {
  display: none;
  position: fixed;
  top: 6rem;
  bottom: 0;
  left: 0;
  width: 100%;
  z-index: 9;
  overflow-y: scroll;
}
@media only screen and (min-width: 1200px) {
  .p-gnav {
    display: block;
    position: static;
    width: auto;
    overflow: visible;
  }
}
.p-gnav__inner {
  background: #fff;
}
@media only screen and (min-width: 1200px) {
  .p-gnav__inner {
    background: transparent;
    display: flex;
    align-items: center;
  }
}
@media only screen and (min-width: 1200px) {
  .p-gnav__list {
    display: flex;
    align-items: center;
    gap: 0 1.5rem;
  }
}
.p-gnav__list > li {
  position: relative;
}
@media only screen and (min-width: 1200px) {
  .p-gnav__list > li:hover .p-gnav__sub-list {
    visibility: visible;
    opacity: 1;
  }
}
.p-gnav__list > li > span,
.p-gnav__list > li > a {
  border-bottom: 1px solid #EDE8E1;
  font-size: 1.6rem;
  padding: 1.5rem 1rem;
  display: block;
  width: 100%;
  position: relative;
  cursor: pointer;
}
@media only screen and (min-width: 1200px) {
  .p-gnav__list > li > span,
  .p-gnav__list > li > a {
    border-bottom: none;
    width: auto;
  }
}
@media only screen and (min-width: 834px) {
  .p-gnav__list > li > span {
    padding: 1.5rem 2rem 1.5rem 1rem;
  }
}
.p-gnav__list > li > span::after {
  content: "\f107";
  right: 2rem;
  color: #FF9D26;
  font-size: 1.6rem;
  transition: transform 0.3s;
  transform-origin: 50% 50%;
  font-family: "Font Awesome 6 Free";
  display: inline-block;
  font-weight: 900;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
}
@media only screen and (min-width: 1200px) {
  .p-gnav__list > li > span::after {
    right: 0;
  }
}
.p-gnav__list > li > span.active::after {
  transform: rotate(180deg) translateY(50%);
}
.p-gnav__list a {
  color: #555555;
}
.p-gnav__sub-list {
  display: none;
}
@media only screen and (min-width: 1200px) {
  .p-gnav__sub-list {
    display: block;
    visibility: hidden;
    opacity: 0;
    transition: all 0.3s;
    position: absolute;
    bottom: 0;
    left: 1rem;
    width: auto;
    padding: 1rem 1.5rem 1rem 2.5rem;
    background: #fff;
    border-radius: 1rem;
    transform: translateY(100%);
    filter: drop-shadow(0 0 10px rgba(0, 0, 0, 0.3));
  }
}
@media only screen and (min-width: 1200px) {
  .p-gnav__sub-list::before {
    content: "";
    position: absolute;
    left: 3rem;
    top: -1rem;
    width: 2.1rem;
    height: 1.1rem;
    background: #fff;
    clip-path: polygon(50% 0%, 0% 100%, 100% 100%);
  }
}
.p-gnav__sub-list > li > a {
  display: block;
  padding: 1.2rem 1rem 1.1rem 0;
  margin-left: 3rem;
  border-bottom: 1px solid #EDE8E1;
  position: relative;
  white-space: nowrap;
}
@media only screen and (min-width: 1200px) {
  .p-gnav__sub-list > li > a {
    border-bottom: none;
    margin-left: 0;
    padding: 0.4rem 1rem 0.4rem 1.5rem;
  }
}
.p-gnav__sub-list > li > a::after {
  content: "\f107";
  color: #FF9D26;
  position: absolute;
  transform: rotate(-90deg);
  top: 1rem;
  right: 2rem;
  font-family: "Font Awesome 6 Free";
  display: inline-block;
  font-weight: 900;
}
@media only screen and (min-width: 1200px) {
  .p-gnav__sub-list > li > a::after {
    top: 0.5rem;
    right: auto;
    left: 0;
  }
}
